export default {
  name: "withdrawLogList",
  data() {
    var validateAmount = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      setTimeout(() => {
        const reg = /^[0-9]+(.[0-9]{1,2})?$/
        if (!reg.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 1000);
    };

    return {
      fullscreenLoading: false,
      sendNotifyDrawerVisable: false,
      direction: 'rtl',
      createTime: [],
      withdrawTime: [],
