          if (this.searchForm.startCreateTime) {
            // 
            var startCreateTime = new Date(this.searchForm.startCreateTime).valueOf() - 8.64e7 + 1;
          } else {
            var startCreateTime = Date.now() - 8.64e7;
          }
          return time && time.valueOf() < startCreateTime
        }
      },
      pickerOptionsStartPay: {},
      pickerOptionsEndPay: {
        disabledDate(time) {
          if (this.searchForm.startWithdrawTime) {
            // 
            var startWithdrawTime = new Date(this.searchForm.startWithdrawTime).valueOf() - 8.64e7 + 1;
          } else {
            var startWithdrawTime = Date.now() - 8.64e7;
          }
          return time && time.valueOf() < startWithdrawTime
        }
      },

      withdrawTypeOptions: [],
      sendNotifyMsgForm: {
        id: '',
        url: '',
        notifyData: '',
        notifyResp: '',
        notifyAnalyze: ''
      },
      formLabelWidth: 120,
      sending: false,
      showCheckBox: true,
      multipleSelection: [],
      showExtra: false,
      createAdminWithdrawDrawerVisible: false,
      adminWithdrawForm: {
        country: 'COL',
        type: '1',
        appId: 10000,
        amount: 0,
        bankAccountName: '',
        bankCardNo: '',
        bankCode: '',
        branchCode: '',
        documentId: '',
        documentType: 'CC',
        accountType: '',
        verifyCode: ''
      },
      adminWithdrawRule: {
        country: [
          {required: true, message: '', trigger: 'change'},
        ],
        type: [
          {required: true, message: '', trigger: 'change'},
        ],
        appId: [
          {required: true, message: 'id', trigger: 'change'},
        ],
        bankCode: [
          {required: true, message: '', trigger: 'blur'},
        ],
        amount: [
          {required: true, validator: validateAmount, trigger: 'blur'}
        ],
        bankCardNo: [
          {required: true, message: '', trigger: 'blur'},
        ],
        documentId: [
          {required: true, message: '', trigger: 'blur'},
        ],
      },
      creatingAdminWithdraw: false
    }
  },
  created() {
    this.queryAllWithdrawLog()
    this.queryCountryPageList()
    this.queryAllWithdrawTypeList()
  },
  methods: {
    queryAllWithdrawLog() {
      var that = this
      that.fullscreenLoading = true
      this.searchForm.startCreateTime = this.createTime === "" || this.createTime === null ? "" : this.createTime[0]
      this.searchForm.endCreateTime = this.createTime === "" || this.createTime === null ? "" : this.createTime[1]
      this.searchForm.startWithdrawTime = this.withdrawTime === "" || this.withdrawTime === null ? "" : this.withdrawTime[0]
      this.searchForm.endWithdrawTime = this.withdrawTime === "" || this.withdrawTime === null ? "" : this.withdrawTime[1]

      queryWithdrawLogPageList(that.searchForm).then(function (response) {
        if (response.data.code === 0) {
          var tableData = []

          var list = response.data.data.list
          var statictics = response.data.data.statictics

          for (var withdrawlog of list) {
            withdrawlog.type = formatWithdrawType(withdrawlog)

            withdrawlog.amount = formatAmount(withdrawlog.amount)
            withdrawlog.platformFee = formatAmount(withdrawlog.platformFee)
            withdrawlog.thirdFee = formatAmount(withdrawlog.thirdFee)

            var status = withdrawlog.status
            status = status === 2 ? '' : status === -2 ? '' : status === -1 ? '' : status === 0 ? '' : ''
            withdrawlog.status = status

            var syncCnt = withdrawlog.syncCnt
            syncCnt = syncCnt === 1 ? '' : syncCnt === 0 ? '' : '' + (syncCnt * -1) + ''
            withdrawlog.syncCnt = syncCnt

            withdrawlog.createTime = dateFormate(withdrawlog.createTime)
            withdrawlog.withdrawTime = dateFormate(withdrawlog.withdrawTime)

            tableData.push(withdrawlog)
          }
          that.tableData = tableData
          that.searchStatictics.totalWithdrawAmount = formatAmount(statictics.totalWithdrawAmount)
          that.searchStatictics.totalWithdrawSuccessAmount = formatAmount(statictics.totalWithdrawSuccessAmount)
          that.searchStatictics.totalWithdrawSuccessFee = formatAmount(statictics.totalWithdrawSuccessFee)
          that.searchStatictics.totalWithdrawSuccessNum = statictics.totalWithdrawSuccessNum
          that.searchStatictics.totalWithdrawProcessAmount = formatAmount(statictics.totalWithdrawProcessAmount)
          that.searchStatictics.totalWithdrawFailAmount = formatAmount(statictics.totalWithdrawFailAmount)
          that.total = statictics.totalWithdrawNum
          that.fullscreenLoading = false
        }
      })
    },
    queryCountryPageList() {
      let that = this
      return new Promise(resolve => {
        queryCountryPageList({type: 'country'}).then(function (res) {
          if (res.data.code === 0) {
            that.countryOptions = []
            res.data.data.forEach((item, index) => {
              that.countryOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    search() {
      this.queryAllWithdrawLog()
    },
    resetForm(name) {
      this.$refs.searchForm.resetFields()
      this.createTime = []
      this.withdrawTime = []
    },
    pageChange(cur) {
      this.searchForm.page = cur
      this.queryAllWithdrawLog()
    },
    closeDialog() {
      this.dialog.visible = false
      this.dialog.isInsert = true
      // this.commonCloseDialog(this.dialog.form, 'ruleForm')
    },
    queryAllWithdrawTypeList() {
      var that = this
      queryWithdrawTypeSelectList().then(function (res) {
        if (res.data.code === 0) {
          that.withdrawTypeOptions = []
          console.log(res.data.data)
          var data = res.data.data
          for (var withdrawType of data) {
            that.withdrawTypeOptions.push({
              label: withdrawType.name,
              value: withdrawType.id
            })
          }
        }
      })
    },
    changeStatus(data) {
      var that = this
      changeWithdrawLogStatus({id: data.id}).then(function (response) {
        if (response.data.code === 0) {
          that.queryAllWithdrawLog()
        }
      })
    },
    openSendNotifyMsgDrawer(data) {
      var that = this
      sendNotifyMsg({id: data.id}).then(function (response) {
        if (response.data.code === 0 || response.data.code === -1) {
          that.queryAllWithdrawLog()
        }
        that.sending = false
      })
    },
    sendNotifyMsg(id) {
      var that = this
      that.sending = true
      return new Promise(resolve => {
        axios({
          method: 'post',
          url: '../withdrawlog/sendNotifyMsg',
          data: 'id=' + id
        }).then(function (response) {
          if (response.data.code === 0 || response.data.code === -1) {
            var data = response.data.data
            that.sendNotifyMsgForm.notifyData = JSON.stringify(data.notifyData)
            that.sendNotifyMsgForm.notifyResp = data.notifyResp

            if (data.notifyResp + '' == 'SUCCESS') {
              that.sendNotifyMsgForm.notifyAnalyze = ''
              that.queryAllWithdrawLog()
            } else {
              that.sendNotifyMsgForm.notifyAnalyze = '' +
                '1."SUCCESS"' +
                '2.' +
                '' +
                '1.' +
                '2.' +
                '3.JSON' +
                '4.' +
                '5.'
            }
          }
          that.sending = false
        })

      });
    },
    handleClose(done) {
      done();
    },
    cancelNotifyMsgDrawer() {
      this.sendNotifyDrawerVisable = false
      this.sendNotifyMsgForm.notifyData = ''
      this.sendNotifyMsgForm.notifyResp = ''
      this.sendNotifyMsgForm.notifyAnalyze = ''
    },
    handleSelectionChange() {

    },
    batchPassWithdrawLog() {
      var ids = []
      ids = Array.from(this.$refs.multipleTable.selection, ({id}) => id)
      this.pass(ids.toString())
    },
    singlePassWithdrawLog(id) {
      this.pass(id)
    },
    batchRejectWithdrawLog() {
      var ids = []
      ids = Array.from(this.$refs.multipleTable.selection, ({id}) => id)
      this.reject(ids.toString())
    },
    singleRejectWithdrawLog(id) {
      this.reject(id)
    },
    pass(ids) {
      var that = this
      this.fullscreenLoading = true
      updateWithdrawLogConfirmStatusPass({ids: ids}).then(function (res) {
        if (res.data.code === 0) {
          that.$message(res.data.msg)
        } else {
          that.$message(res.data.msg)
        }
        that.queryAllWithdrawLog()
      })
    },
    reject(ids) {
      const that = this;
      this.fullscreenLoading = true

      updateWithdrawLogConfirmStatusReject({ids: ids}).then(function (res) {
        if (res.data.code === 0) {
          that.$message(res.data.msg)
        } else {
          that.$message(res.data.msg)
        }
        that.queryAllWithdrawLog()
      })
    },
    handleSizeChange(val) {
      this.searchForm.limit = val
      this.queryAllWithdrawLog()
    },
    createAdminWithdrawDrawerHandleClose() {
      this.createAdminWithdrawDrawerVisible = false
    },
    createAdminWithdrawLog() {
      const that = this

      that.$refs['adminWithdrawForm'].validate((valid) => {
        if (valid) {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            that.creatingAdminWithdraw = true

            getWithdrawLogToken().then((resp) => {
              const code = resp.data.code
              if (code === 0) {
                const token = resp.data.data

                addAdminWithdrawLogColombia(token, that.adminWithdrawForm).then((response) => {
                  if (response !== null) {
                    const code = response.data.code
                    const msg = response.data.msg
                    that.$message(msg)
                    if (code === 0) {
                      that.queryAllWithdrawLog()
                      that.adminWithdrawForm.amount = 0
                      that.adminWithdrawForm.bankAccountName = ''
                      that.adminWithdrawForm.bankCardNo = ''
                      that.adminWithdrawForm.bankCode = ''
                      that.adminWithdrawForm.branchCode = ''
                      that.adminWithdrawForm.documentId = ''
                      that.adminWithdrawForm.documentType = ''
                    } else {
                    }
                    that.creatingAdminWithdraw = false
                  }
                })
              }
            })


          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        } else return false;
      })
    }
  }
}
</script>

<style scoped>

</style>



