<template>
  <div id="app" style="">
    <!--            form-->
    <el-form :model="searchForm" ref="searchForm" label-width="90px">
      <el-row>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="UTR" prop="utr">
            <el-input v-model="searchForm.utr" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="orderId">
            <el-input v-model="searchForm.orderId" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <!--        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">-->
        <!--          <el-form-item label="" prop="transId">-->
        <!--            <el-input v-model="searchForm.transId" placeholder=""></el-input>-->
        <!--          </el-form-item>-->
        <!--        </el-col>-->
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="syncStatus">
            <el-select v-model="searchForm.syncStatus" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="payAccount">
            <el-input v-model="searchForm.payAccount" placeholder=""></el-input>
          </el-form-item>
        </el-col>

        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="" prop="amount">
            <el-input v-model="searchForm.amount" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <!--        <el-col :xl="12" :lg="12" :md="12" :sm="24" :xs="24" class="form-search">-->
        <!--          <el-form-item label="">-->
        <!--            <el-col :span="11">-->
        <!--              <el-date-picker-->
        <!--                v-model="createTime"-->
        <!--                type="datetimerange"-->
        <!--                :clearable="true"-->
        <!--                :default-time="['00:00:00', '23:59:59']"-->
        <!--                value-format="yyyy-MM-dd HH:mm:ss"-->
        <!--                placeholder=""-->
        <!--                :picker-options="pickerOptionsStartCreate">-->
        <!--              </el-date-picker>-->
        <!--            </el-col>-->
        <!--          </el-form-item>-->
        <!--        </el-col>-->
      </el-row>
      <el-form-item class="search-button">
        <el-button size="medium" icon="el-icon-search" type="primary" @click="search"></el-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <!--            -->
    <div id="grid" style="width: 100%">
      <el-table
        :data="tableData"
        style="width: 100%"
        size="small">
        <el-table-column type="expand">
          <template slot-scope="props">
            <!--            <el-form label-position="left" inline class="demo-table-expand">-->
            <!--              <el-form-item label="">-->
            <!--                <span>{{ props.row.callbackUrl }}</span>-->
            <!--              </el-form-item>-->
            <!--            </el-form>-->
          </template>
        </el-table-column>
        <el-table-column
          prop="type"
          label="">
        </el-table-column>
        <el-table-column
          prop="utr"
          label="utr">
        </el-table-column>
        <el-table-column
          prop="orderId"
          label="">
        </el-table-column>
        <el-table-column
          prop="pOrderId"
          label="">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="">
        </el-table-column>
        <!--        <el-table-column-->
        <!--          prop="transId"-->
        <!--          label="">-->
        <!--        </el-table-column>-->
        <el-table-column
          prop="creditAmount"
          label="">
        </el-table-column>
        <el-table-column
          prop="debitAmount"
          label="">
        </el-table-column>
        <el-table-column
          prop="payAccount"
          label="">
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span v-if="scope.row.syncStatus === ''"
                  style="color: #67c24a;font-size: 12px;font-family: 'Microsoft YaHei',serif">{{scope.row.syncStatus}}</span>
            <span v-else style="color: #ff0000;font-size: 12px;font-family: 'Microsoft YaHei',serif">{{scope.row.syncStatus}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="account"
          label="">
        </el-table-column>
        <el-table-column
          prop="accountBalance"
          label="">
        </el-table-column>
<!--        <el-table-column label="" width="300">-->
<!--          <template slot-scope="scope">-->
<!--            <el-button v-if="(scope.row.syncStatus === '' )" type="danger"-->
<!--                       @click="openMatchPayLogDrawer(scope)"-->
<!--                       @change="">-->
<!--            </el-button>-->
<!--            &lt;!&ndash;            <el-button :disabled="(scope.row.status === '' )"&ndash;&gt;-->
<!--            &lt;!&ndash;                       type="success"&ndash;&gt;-->
<!--            &lt;!&ndash;                       round&ndash;&gt;-->
<!--            &lt;!&ndash;                       @click="openSendNotifyMsgDrawer(scope.row)"&ndash;&gt;-->
<!--            &lt;!&ndash;                       @change="">&ndash;&gt;-->
<!--            &lt;!&ndash;            </el-button>&ndash;&gt;-->
<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>
    </div>
    <!--            -->
    <!--            <div style="text-align: center;margin-top:5px;">-->
    <!--                <el-pagination-->
    <!--                        background-->
    <!--                        layout="total, prev, pager, next"-->
    <!--                        :total="total" :current-page="searchForm.page" @current-change="pageChange">-->
    <!--                </el-pagination>-->
    <!--            </div>-->
    <div style="text-align: center;margin-top:5px;height: 100%">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="pageChange"
        :current-page.sync="searchForm.page"
        :page-size="searchForm.limit"
        :page-sizes="[10, 20, 50, 100, 200]"
        layout="total, sizes, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <el-drawer
      :visible.sync="matchPayLogDrawerVisible"
      :direction="direction"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :show-close="false"
      custom-class="demo-drawer"
      style="overflow-y: auto"
      size="50%">
      <div class="" style="padding: 20px">
        <el-form>
          <el-form-item label="/" :label-width="formLabelWidth">
            <el-input readonly v-model="matchPayLogForm.orderId" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button :disable="matching" type="success" @click="matchPayLog()">{{matching
            ?
            '...' : ''}}
          </el-button>
          <el-button @click="cancelNotifyMsgDrawer()"> </el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>
<script>
  import {matchPayLog, queryIcIcBankRecordPageList} from "../../api/icIcBankRecord";


  export default {
    name: "IcIcBankRecordList",
    data() {
      return {
        fullscreenLoading: false,
        centerDialogVisible: false,
        matchPayLogDrawerVisible: false,
        innerVisible: false,
        sendNotifyDrawerVisable: false,
        direction: 'rtl',
        password: '',
        verifyCode: '',
        notifyId: '',
        createTime: [],
        payTime: [],
        countryOptions: [],
        payTypeOptions: [],
        searchFormOri: {},
        searchForm: {
          id: '',
          orderId: '',
          utr: '',
          transId: '',
          type: '',
          syncStatus: '',
          payAccount: '',
          amount: '',
          page: 1,
          limit: 10,
          rowIndex: 0
        },
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {},
          settings: {
            visible: false,
            activeName: 'first',
            userName: '',
            appId: 0,
            payFix: 0,
            payRate: 0,
            withdrawFix: 0,
            withdrawRate: 0,
            balanceForm: {
              password: '',
              Verifycode: '',
              remain: 0,
              Notes: ''
            }
          },
          rules: {
            UserName: [
              {required: true, message: '', trigger: 'blur'},
              {min: 3, max: 50, message: ' 3  50 ', trigger: 'blur'}
            ]
          },
        },
        tableData: [],
        total: 0,
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.searchForm.startCreateTime) {
              // 
              var startCreateTime = new Date(this.searchForm.startCreateTime).valueOf() - 8.64e7 + 1;
            } else {
              var startCreateTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startCreateTime
          }
        },
        pickerOptionsStartPay: {},
        pickerOptionsEndPay: {
          disabledDate(time) {
            if (this.searchForm.startPayTime) {
              // 
              var startPayTime = new Date(this.searchForm.startPayTime).valueOf() - 8.64e7 + 1;
            } else {
              var startPayTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startPayTime
          }
        },
        notifyBtnSwitch: 'disabled',
        matchPayLogForm: {
          id: '',
          orderId: '',
        },
        formLabelWidth: 120,
        matching: false,
        searchStatictics: {
          needPayAmount: 0,
          payAmount: 0,
          payFee: 0,
          payNum: 0
        }
      }
    },
    created() {
      this.queryIcIcBankRecordPageList()
      this.searchFormOri = this.searchForm
    },
    methods: {
      queryIcIcBankRecordPageList() {
        const that = this;
        that.fullscreenLoading = true

        return new Promise(resolve => {
          queryIcIcBankRecordPageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {
              const tableData = [];

              const statistics = response.data.data.statistics;
              const list = response.data.data.list;

              for (const icIcBankRecord of list) {
                icIcBankRecord.syncStatus = icIcBankRecord.syncStatus === 1 ? '' : ''

                tableData.push(icIcBankRecord)
              }
              that.tableData = tableData
              that.total = statistics.count
              that.fullscreenLoading = false
            }
          })
        });
      },

      openMatchPayLogDrawer(scope) {
        this.matchPayLogForm.id = scope.row.id
        this.matchPayLogForm.orderId = scope.row.orderId
        this.matchPayLogDrawerVisible = true
      },

      matchPayLog() {
        matchPayLog(this.matchPayLogForm).then((response) => {

        })
      },

      search() {
        this.queryIcIcBankRecordPageList()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.createTime = []
        this.payTime = []
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryIcIcBankRecordPageList()
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      handleClose(done) {
        done();
      },
      handleSizeChange(val) {
        this.searchForm.limit = val
        this.queryAllPayLog()
      },
    }
  }
</script>

<style scoped>

</style>



